import {Component, EventEmitter, Input, Output} from '@angular/core';
import { first } from 'rxjs/operators';
import { CountryService } from '@app/_services';

@Component({
    selector: 'trackday-wizard-step2-contact',
    templateUrl: 'trackday-wizard-step2-contact.component.html',
    styleUrls: ['./trackday-wizard-step2-contact.component.css']
})
export class TrackdayWizardStep2ContactComponent {
    @Input('gender') gender;
    @Output() genderChange = new EventEmitter<string>();

    @Input('firstName') firstName;
    @Output() firstNameChange = new EventEmitter<string>();

    @Input('lastName') lastName;
    @Output() lastNameChange = new EventEmitter<string>();

    @Input('street') street;
    @Output() streetChange = new EventEmitter<string>();

    @Input('zip') zip;
    @Output() zipChange = new EventEmitter<string>();

    @Input('city') city;
    @Output() cityChange = new EventEmitter<string>();

    @Input('country') country;
    @Output() countryChange = new EventEmitter<string>();

    @Input('phone') phone;
    @Output() phoneChange = new EventEmitter<string>();

    @Input('email') email;
    @Output() emailChange = new EventEmitter<string>();

    @Input('activeTabIndex') activeTabIndex: number = 0;

    @Output() onClickContinue = new EventEmitter<void>();
    @Output() onClickCancel = new EventEmitter<void>();

    countries: [];

    submitted: boolean = false;

    constructor(
        private countryService: CountryService,
    ) { }

    ngOnInit() {
        this.countryService.getAll().pipe(first()).subscribe((response: any) => {
            this.countries = response;
        });
    }

    handleClickContinue() {
        this.submitted = true;

        if (!this.gender || !this.firstName || !this.lastName || !this.street || !this.zip || !this.city /*|| !this.country*/ || !this.phone || !this.email) return;

        this.onClickContinue.emit();
    }
}
