import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './home';
import { LoginComponent } from './login';
import { AuthGuard } from './_helpers';
import { TrackdayComponent } from './trackday-list';
import { TrackdayWizardComponent } from './trackday-wizard';

const routes: Routes = [
    // { path: '', component: HomeComponent, canActivate: [AuthGuard] },
    { path: 'angular', component: TrackdayComponent /*, canActivate: [AuthGuard]*/ },
    // { path: 'trackday', component: TrackdayComponent,/* canActivate: [AuthGuard] */},
    { path: 'angular/:id', component: TrackdayWizardComponent, /*canActivate: [AuthGuard]*/ },
    { path: 'login', component: LoginComponent },

    // otherwise redirect to home
    { path: '**', redirectTo: '/login' }
];

export const appRoutingModule = RouterModule.forRoot(routes);