import {Component, EventEmitter, Input, Output} from '@angular/core';
import { first } from 'rxjs/operators';
import { MakeService, ModelService, SerieService, TrimService } from "@app/_services";

@Component({
    selector: 'trackday-wizard-step3-vehicle',
    templateUrl: 'trackday-wizard-step3-vehicle.component.html',
    styleUrls: ['./trackday-wizard-step3-vehicle.component.css']
})
export class TrackdayWizardStep3VehicleComponent {
    makes: [];
    models: [];
    series: [];
    trims: [];

    @Input('make') make;
    @Output() makeChange = new EventEmitter<string>();

    @Input('model') model;
    @Output() modelChange = new EventEmitter<string>();

    @Input('serie') serie;
    @Output() serieChange = new EventEmitter<string>();

    @Input('trim') trim;
    @Output() trimChange = new EventEmitter<string>();

    @Input('licenseplate') licenseplate;
    @Output() licenseplateChange = new EventEmitter<string>();

    @Input('activeTabIndex') activeTabIndex: number = 0;

    @Output() onClickContinue = new EventEmitter<void>();
    @Output() onClickCancel = new EventEmitter<void>();

    submitted: boolean = false;

    constructor(
        private makeService: MakeService,
        private modelService: ModelService,
        private serieService: SerieService,
        private trimService: TrimService,
    ) { }

    ngOnInit() {
        this.makeService.getAll().pipe(first()).subscribe((response: any) => {
            this.makes = response;
        });
    }

    public onChangeLicensePlate() {
        this.licenseplateChange.emit(this.licenseplate);
    }

    public handleChangeMake() {
        this.makeChange.emit(this.make);
        this.modelService.getAll(this.make).pipe(first()).subscribe((response: any) => {
            this.models = response;
        });
    }

    public handleChangeModel() {
        this.modelChange.emit(this.model);
        this.serieService.getAll(this.model).pipe(first()).subscribe((response: any) => {
            this.series = response;
        });

        if (this.trim) {
            this.trimService.getAll(this.model, this.serie).pipe(first()).subscribe((response: any) => {
                this.trims = response;
            });
        }
    }

    public handleChangeSerie() {
        this.serieChange.emit(this.serie);
        if (this.model) {
            this.trimService.getAll(this.model, this.serie).pipe(first()).subscribe((response: any) => {
                this.trims = response;
            });
        }
    }

    handleClickContinue() {
        this.submitted = true;

        if (!this.make || !this.model || !this.serie || !this.trim || !this.licenseplate) return;

        this.onClickContinue.emit();
    }
}
