import {Component} from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import {first} from "rxjs/operators";
import {TrackdayService} from "@app/_services";

@Component({
    templateUrl: 'trackday-wizard.component.html',
    styleUrls: ['./trackday-wizard.component.css']
})
export class TrackdayWizardComponent {
    activeTabIndex: number = 0;
    countries: [];

    trackday;

    constructor(
        private route: ActivatedRoute,
        private trackdayService: TrackdayService
    ) {

    }

    ngOnInit() {
        this.route.params.subscribe(params => {
            this.trackdayService.get(params.id).pipe(first()).subscribe((response: any) => {
                const trackday = response.data;

                if (trackday) {
                    trackday.vehicles = Object.keys(trackday.vehicles).map(key => trackday.vehicles[key]);
                    this.trackday = trackday;
                }
            });
        });
    }
}
