import { Component } from '@angular/core';
import { first } from 'rxjs/operators';

import { TrackdayService } from '@app/_services';
import * as mapboxgl from 'mapbox-gl';
import {environment} from "@environments/environment";

declare var L: any;

@Component({
    templateUrl: 'trackday.component.html',
    styleUrls: ['./trackday.component.css']
})
export class TrackdayComponent {
    loading = false;
    trackdays: [];

    map: mapboxgl.Map;
    style = 'mapbox://styles/mapbox/light-v9';
    lat = 37.75;
    lng = -122.41;

    constructor(private trackdayService: TrackdayService) { }

    ngOnInit() {
        this.loading = true;

        // mapboxgl.accessToken = environment.mapbox.accessToken;
        this.map = new mapboxgl.Map({
            accessToken: environment.mapbox.accessToken,
            container: 'map',
            style: this.style,
            zoom: 2,
            center: [0, 0]
        });
        // Add map controls
        this.map.addControl(new mapboxgl.NavigationControl());

        this.map.on('load', () => {
            this.trackdayService.getAll().pipe(first()).subscribe((response: any) => {
                this.loading = false;

                const trackdays = response.data.map(trackday => ({
                    ...trackday,
                    vehicles: Object.keys(trackday.vehicles).map(key => trackday.vehicles[key]),
                }));
                this.trackdays = trackdays;

                trackdays.forEach((trackday, i) => {
                    this.map.addLayer({
                        "id": "points" + i,
                        "type": "circle",
                        "paint": {
                            "circle-radius": 5,
                            "circle-color": '#ff0000'
                        },
                        "source": {
                            "type": "geojson",
                            "data": {
                                "type": "FeatureCollection",

                                "features": [{
                                    "type": "Feature",
                                    "properties": {
                                        "field": null
                                    },
                                    "geometry": {
                                        "type": "Point",
                                        "coordinates": [trackday.lon, trackday.lat]
                                    }
                                }]
                            }
                        }
                    });
                });
            });
        });
    }
}
