import {Component, EventEmitter, Input, Output} from '@angular/core';

@Component({
    selector: 'trackday-wizard-steps',
    templateUrl: 'trackday-wizard-steps.component.html',
    styleUrls: ['./trackday-wizard-steps.component.css']
})
export class TrackdayWizardStepsComponent {
    @Input('activeTabIndex') activeTabIndex: number = 0;
    @Output() activeTabIndexChange = new EventEmitter<number>();

    profileType = 'guest';
    gender;
    firstName;
    lastName;
    address;
    zip;
    city;
    country;
    phone;
    email;
    make;
    model;
    serie;
    trim;
    licenseplate;

    constructor() { }

    ngOnInit() {
    }

    handleClickContinue() {
        this.activeTabIndexChange.emit(this.activeTabIndex + 1);
    }

    handleClickCancel() {
        this.activeTabIndexChange.emit(this.activeTabIndex - 1);
    }
}
